		UpdateBundle(index){
			this.tmp_value=index;
			this.BundleItems=[{}];
			this.BundleItems.splice(0,Object.keys(this.BundleItems).length);
			var cnt =0;
			for(var x=0; x<this.partItems.length;x++){
				if(this.partItems[index].clm_m_material_id==this.partItems[x].clm_m_material_id && (this.partItems[x].clm_m_gubun==''  || this.partItems[x].clm_m_gubun=='U' || this.partItems[x].clm_m_gubun=='LU')){
					cnt = cnt+Number(this.partItems[x].clm_m_part_count)+Number(this.partItems[x].clm_m_poor_count);
					this.BundleItems.push({
						clm_material_name:this.partItems[this.tmp_value].clm_m_part_name,
						clm_material_count:Number(this.partItems[x].clm_m_part_count),
						clm_poor_count:Number(this.partItems[x].clm_m_poor_count)
					});
				}
			}
			this.clm_m3_material_total_count =cnt;
			this.clm_m3_material_left_count=0;
			this.BundleModal=true;
		},
		bundlePlus(){
			var cnt =0;
			for(var x=0; x< this.BundleItems.length;x++){
				cnt = cnt+Number(this.BundleItems[x].clm_material_count);
			}
			if(Number(this.clm_m3_material_total_count) <=cnt){
				alert('자재 총 수량을 모두 소진하였습니다.');
				return;
			}
			this.BundleItems.push({
				clm_material_name:this.partItems[this.tmp_value].clm_m_part_name,
				clm_material_count:Number(this.clm_m3_material_left_count),
				clm_poor_count:0
			});
			this.clm_m3_material_left_count=0;
		},
		bundleDelete(index){
			this.BundleItems.splice(index,1);
			var cnt =0;
			for(var x=0; x< this.BundleItems.length;x++){
				cnt = cnt+Number(this.BundleItems[x].clm_material_count)+Number(this.BundleItems[x].clm_poor_count);
			}
			this.clm_m3_material_left_count=Number(this.clm_m3_material_total_count)-cnt;
		},
		bundleCountChange(value,index){
			var cnt =0;
			for(var x=0; x< this.BundleItems.length;x++){
				cnt = cnt+Number(this.BundleItems[x].clm_material_count)+Number(this.BundleItems[x].clm_poor_count);
			}
			this.clm_m3_material_left_count=Number(this.clm_m3_material_total_count)-cnt;
			if(value=='m'){
				this.BundleItems[index].clm_poor_count=0;
				if(Number(this.clm_m3_material_left_count)+Number(this.BundleItems[index].clm_poor_count)<0){
					alert('자재 총 수량을 초과하였습니다.');
					var cnt =0;
					for(var x=0; x< this.BundleItems.length;x++){
						if(index!=x){
							cnt = cnt+Number(this.BundleItems[x].clm_material_count)+Number(this.BundleItems[x].clm_poor_count);
						}
					}
					this.BundleItems[index].clm_material_count= Number(this.clm_m3_material_total_count)-cnt- Number(this.BundleItems[index].clm_poor_count);
					this.clm_m3_material_left_count=0;
				}
				this.BundleItems[index].clm_poor_count=0;
			}else{
				if(Number(this.BundleItems[index].clm_material_count)<Number(this.BundleItems[index].clm_poor_count)){
					alert('자재 총 수량을 초과하였습니다.');
					var cnt =0;
					for(var x=0; x< this.BundleItems.length;x++){
						if(index!=x){
							cnt = cnt+Number(this.BundleItems[x].clm_material_count)+Number(this.BundleItems[x].clm_poor_count);
						}
					}
					this.BundleItems[index].clm_poor_count=0;
					// this.BundleItems[index].clm_poor_count= Number(this.clm_m3_material_total_count)-cnt- Number(this.BundleItems[index].clm_material_count);
					// this.clm_m3_material_left_count=0;
				}else{
					this.BundleItems[index].clm_material_count= Number(this.BundleItems[index].clm_material_count)-this.BundleItems[index].clm_poor_count;
				}

			}
			
			var cnt =0;
			for(var x=0; x< this.BundleItems.length;x++){
				cnt = cnt+Number(this.BundleItems[x].clm_material_count)+Number(this.BundleItems[x].clm_poor_count);
			}
			this.clm_m3_material_left_count=Number(this.clm_m3_material_total_count)-cnt;
		},
		saveBundle(){
			//수량 체크
			var cnt =0;
			var error_1 ='N';
			var error_2='N';
			var error_3 ='N';
			var error_poor_1 ='N';
			var error_poor_2='N';
			var error_poor_3 ='N';
			for(var x=0; x< this.BundleItems.length;x++){
