					this.BundleItems[x].clm_poor_count=0;
				}
				cnt = cnt+this.BundleItems[x].clm_material_count;
				if(Number(this.BundleItems[x].clm_material_count)<=0){
					error_1='Y';
				}else if(Number(this.BundleItems[x].clm_material_count)%1 !=0){
					error_2='Y';
				}
				// else if(this.BundleItems[x].clm_material_count=='' || typeof this.BundleItems[x].clm_material_count =='undefined'){
