				if(Number(this.BundleItems[x].clm_poor_count)<0){
					error_poor_1='Y';
				}else if(Number(this.BundleItems[x].clm_poor_count)%1 !=0){
					error_poor_2='Y';
				}
				// else if(this.BundleItems[x].clm_poor_count=='' ){
				// 	error_poor_3='Y';
				// }
			}
			if(Number(this.clm_m3_material_left_count) >0){
				alert('자재 총 수량보다 사용량이 적습니다.');
				return;
			}else if(Number(this.clm_m3_material_left_count)<0){
				alert('사용량이 자재 총 수량을 초과했습니다.');
				return;
			}
			if(error_1=='Y'){
				alert('자재 사용 수량이 0또는 음수가 될 수 없습니다.');
				return;
			}else if(error_2=='Y'){
				alert('자재 사용 수량이 소수가 될 수 없습니다.');
				return;
			}else if(error_3=='Y'){
				alert('자재 수량이 빈칸입니다.');
				return;
			}else if(error_poor_1=='Y'){
				alert('불량 수량은 음수가 될 수 없습니다.');
				return;
			}else if(error_poor_2=='Y'){
				alert('불량 수량은 소수가 될 수 없습니다.');
				return;
			}else if(error_poor_3=='Y'){
				alert('불량 수량이 빈칸입니다.');
				return;
			}
			var clm_m_material_id =this.partItems[this.tmp_value].clm_m_material_id;
			var clm_m_part_name =this.partItems[this.tmp_value].clm_m_part_name;
			var clm_load_repository =this.partItems[this.tmp_value].clm_m_part_location_id;
			var repositoy = this.partItems[this.tmp_value].clm_m_part_repository_position_id_items;
			var clm_load_repository_position =this.partItems[this.tmp_value].clm_m_part_location_position_id;
			var min_cnt =this.partItems.length;
			for(var x=0; x<this.partItems.length;x++){
				if(clm_m_material_id==this.partItems[x].clm_m_material_id &&  this.partItems[x].clm_m_gubun==''){
					if(min_cnt>x){
						min_cnt=x;
					}
					this.partItems.splice(x,1);
					x=x-1;
				}
			}
			var tmp_cnt =Number(this.tmp_value);
			for(var x=0; x< this.BundleItems.length;x++){
				this.partItems.splice(min_cnt, 0, {
					lotno:'',
					clm_m_material_id :clm_m_material_id,
					clm_m_part_width : '0',
