					clm_m_part_count :Number(this.BundleItems[x].clm_material_count),
					// clm_m_poor_count : response.data[x].clm_poor_count,
					clm_m_part_before_count :Number(this.BundleItems[x].clm_material_count),
					clm_m_poor_count :Number(this.BundleItems[x].clm_poor_count),
					clm_m_gubun: '',
