			var id= '';
			var name ='';
			for(var x=0;x<this.user_document_1.length;x++){
				if(this.user_document_1[x].clm_check_yn=='Y'){
					id=id+this.user_document_1[x].clm_user_id+',';
					name=name+this.user_document_1[x].clm_user_name+',';
				}
			}
			for(var x=0;x<this.user_document_2.length;x++){
				if(this.user_document_2[x].clm_check_yn=='Y'){
					id=id+this.user_document_2[x].clm_user_id+',';
					name=name+this.user_document_2[x].clm_user_name+',';
				}
			}
			for(var x=0;x<this.user_document_3.length;x++){
				if(this.user_document_3[x].clm_check_yn=='Y'){
					id=id+this.user_document_3[x].clm_user_id+',';
					name=name+this.user_document_3[x].clm_user_name+',';
				}
			}
			for(var x=0;x<this.user_document_4.length;x++){
