		getList() {
			this.selected_items=[];
			const vm = this
			this.loading = true		
			// this.save_yn='Y';	
			return new Promise((resolve, reject) => {
				const { sortBy, sortDesc, page, itemsPerPage } = this.options
				let items = axios.get('/back/MOB001002/List',{
						params: {
							keyword: this.keyword,
							start_date: this.start_date,
							end_date: this.end_date,
							gubun_id:this.gubun_id,
							mob:'mob001',
							page: page,
							PerPage: itemsPerPage,
							clm_user: this.$session.get('user_id'),
						}
					}).then(
					response => {
						if (response.status == '200') {
							items = response.data
							setTimeout(() => {
								this.loading = false
								this.save_yn='N';
								this.document = items
							}, 500)
						}else{
							this.save_yn='N';
							this.loading = false
							Swal.fire({
								title:'서버가 불안정합니다.\n다시 조회해주세요.',
								icon: 'error'
							});
							this.document =[];
						}
					},
				)
			})
			this.selected_items = [];
		},
