		OpenWorkEndModal(){
			this.loading_yn='N';
			this.changeLoading_yn='N';
			this.LossItems = [{}],
			this.LossItems.splice(0, 1);
				
			this.Workitems=[{}];
			this.Workitems.splice(0, 1);
			if(this.selected_items.length <=0) {
				alert('선택된 재단 작업이 없습니다.');
				return;
			}else if(this.selected_items.length>1){	
				alert('선택된 재단 작업이 한 개 이상입니다.');
				return;
			}
			
			this.m2_cut_joborder_id=this.selected_items[0].clm_cut_joborder_id;
			this.m2_cut_joborder_user=this.selected_items[0].clm_cut_joborder_user_id;
			this.m2_cut_joborder_user_name = this.selected_items[0].clm_cut_joborder_user;
