			this.m2_holl_size=this.selected_items[0].clm_holl_size;
			this.m2_material_name=this.selected_items[0].clm_material_name;
			this.m2_material_type=this.selected_items[0].clm_material_type_id;
			this.m2_material_thickness=this.selected_items[0].clm_material_thickness;
