		async part_CountChange(index){
			this.loading_yn='Y';
			if(this.partItems[index].clm_m_poor_count==''){
				this.partItems[index].clm_m_poor_count=0;
			}
			if((Number(this.partItems[index].clm_m_part_width)<=200|| this.clm_m3_material_type=='0007')){
				if(Number(this.partItems[index].clm_m_part_count)<Number(this.partItems[index].clm_m_poor_count)){
					this.partItems[index].clm_m_poor_count=0;
					this.partItems[index].clm_m_part_count=Number(this.partItems[index].clm_m_part_before_count);
					alert('불량 수량이 재단완료수량을 넘을 수 없습니다.');
					return;
				}else if(Number(this.partItems[index].clm_m_part_count)-Number(this.partItems[index].clm_m_poor_count)<0 ){
					this.partItems[index].clm_m_poor_count=Number(0);
					this.partItems[index].clm_m_part_count=Number(this.partItems[index].clm_m_part_before_count);
					alert('불량 수량이 재단 사용수량을 넘을 수 없습니다.');
					return;
				}else if(Number(this.partItems[index].clm_m_poor_count)<0){
					this.partItems[index].clm_m_poor_count=Number(0);
					this.partItems[index].clm_m_part_count=Number(this.partItems[index].clm_m_part_before_count);
					alert('불량 수량은 음수가 될 수 없습니다.');
					return;
				}else if(Number(this.partItems[index].clm_m_poor_count)%1 !=0){
					this.partItems[index].clm_m_poor_count=Number(0);
					this.partItems[index].clm_m_part_count=Number(this.partItems[index].clm_m_part_before_count);
					alert('불량 수량은 소수가 될 수 없습니다.');
					return;
				}else if(this.partItems[index].clm_m_poor_count!=''){
					this.partItems[index].clm_m_part_count=Number(this.partItems[index].clm_m_part_before_count)-Number(this.partItems[index].clm_m_poor_count);
				}
			}else{
				if(Number(this.m_material_use_count)-Number(this.partItems[index].clm_m_poor_count)<0 ){
					this.partItems[index].clm_m_poor_count=Number(0);
					this.partItems[index].clm_m_part_count=Number(this.partItems[index].clm_m_part_before_count);
					alert('불량 수량이 재단 사용수량을 넘을 수 없습니다.');
					return;
				}else if(Number(this.partItems[index].clm_m_poor_count)<0){
					this.partItems[index].clm_m_poor_count=Number(0);
					this.partItems[index].clm_m_part_count=Number(this.partItems[index].clm_m_part_before_count);
					alert('불량 수량은 음수가 될 수 없습니다.');
					return;
				}else if(Number(this.partItems[index].clm_m_poor_count)%1 !=0){
					this.partItems[index].clm_m_poor_count=Number(0);
					this.partItems[index].clm_m_part_count=Number(this.partItems[index].clm_m_part_before_count);
					alert('불량 수량은 소수가 될 수 없습니다.');
					return;
				}else if(this.partItems[index].clm_m_poor_count!=''){
					this.partItems[index].clm_m_part_count=Number(this.partItems[index].clm_m_part_before_count)-Number(this.partItems[index].clm_m_poor_count);
				}
			}		
			setTimeout(() => {
				this.loading_yn='N';
			}, 1000)
		},
