		async WorkEndComplete(){//재단완료 등록
			if(this.loading_yn=='Y'){
				alert('저장중입니다.잠시만 기다려주세요.');
				return;
			}
			this.loading_yn='Y';
			this.changeLoading_yn= 'Y';
			var end_yn ='N';
			if(this.m2_cut_produce_count=='' || Number(this.m2_cut_produce_count)==0){
				alert('재단 완료 수량을 입력해주시기 바랍니다.');
				this.loading_yn='N';
				this.changeLoading_yn='N';
				return;
			}else if(Number(this.m2_cut_produce_count)<0 || Number(this.m2_cut_produce_count)% 1!= 0){
