		repositoryChange(){
			this.clm_m_material_location_position_id='';
			axios.get('/back/SCR003008/RepositoryPositionList',{
				params: {
		          repository_id: this.clm_m_material_location_id
		        }
			})
			.then(response => {
				this.clm_m_repository_position_id_items = response.data;
				for(var x=0;x<this.partItems.length;x++){
					this.partItems[x].clm_m_part_location_id=this.clm_m_material_location_id;
					this.partItems[x].clm_m_part_repository_position_id_items = response.data;
					this.partItems[x].clm_m_part_location_position_id='';
				}
			})
			.catch(error => {
				console.log(error)
			})
		},
		repositoryPositionChange(){
			for(var x=0;x<this.partItems.length;x++){
				if(this.partItems[x].clm_m_part_location_id=this.clm_m_material_location_id){
					this.partItems[x].clm_m_part_location_position_id=this.clm_m_material_location_position_id
				}
			}
		},
		part_repositoryChange(index){
			if(this.partItems.length-1>=index){
				this.partItems[index].clm_m_part_location_position_id = '';
				axios.get('/back/SCR003008/RepositoryPositionList',{
					params: {
					  repository_id: this.partItems[index].clm_m_part_location_id
					}
				})
				.then(response => {
					this.partItems[index].clm_m_part_repository_position_id_items = response.data;
				})
				.catch(error => {
					console.log(error)
				});
			}
		},
		async Save(){
			if(this.loading_yn=='Y'){
				alert('저장중입니다.잠시만 기다려주세요.');
				return;
			}
			this.loading_yn='Y';
			this.changeLoading_yn='Y';
			var partData = '';
			var Error_count = 'N';
			var Error_count_minus2 = 'N';
			var Error_location = 'N';
			var Error_position = 'N';
			var Error_count2 = 'N';
			var Error_count3 = 'N';

			if(this.partItems.length > 0){
				for(var x=0; x<this.partItems.length; x++){
					if(partData != ''){
						partData += '▒';
					}
					if(this.partItems[x].clm_m_poor_count == ''){
						this.partItems[x].clm_m_poor_count = 0;
					}
					if(Number(this.partItems[x].clm_m_part_count)<0 || Number(this.partItems[x].clm_m_part_count)% 1!= 0){
						Error_count = 'Y';
					}
					if(Number(this.partItems[x].clm_m_poor_count)<0 || Number(this.partItems[x].clm_m_poor_count)% 1!= 0){
						Error_count2 = 'Y';
					}
					if(Number(this.partItems[x].clm_m_poor_count)==0 && Number(this.partItems[x].clm_m_part_count)==0){
						Error_count3 = 'Y';
					}
					if(this.partItems[x].clm_m_part_location_id=='' || typeof this.partItems[x].clm_m_part_location_id=='undefined'){
						Error_location = 'Y';
					}
					if(this.partItems[x].clm_m_part_location_position_id=='' || typeof this.partItems[x].clm_m_part_location_position_id=='undefined'){
						Error_position = 'Y';
					}
					if(this.partItems[x].clm_m_poor_count =='' || Number(this.partItems[x].clm_m_poor_count)==0){
						this.partItems[x].clm_m_poor_count= '0';
					}
					if(this.partItems[x].clm_m_gubun=='' || typeof this.partItems[x].clm_m_gubun=='undefined'){
						this.partItems[x].clm_m_gubun= ' ';
					}
					if(this.partItems[x].clm_m_part_count=='' || typeof this.partItems[x].clm_m_part_count=='undefined'){
						this.partItems[x].clm_m_part_count= ' ';
					}
					partData +=Number( this.partItems[x].clm_m_poor_count)+ '▦'+ this.partItems[x].clm_m_part_location_id+'▦'+this.partItems[x].clm_m_part_location_position_id+'▦'+Number(this.partItems[x].clm_m_part_count)+'▦'+this.partItems[x].clm_m_material_id+'▦'+this.partItems[x].clm_m_gubun;
				}
			}else if(this.partItems.length == 0){
				alert('재단 자재가 없습니다.');
				this.loading_yn='N';
				this.changeLoading_yn='N';
				return;
			}
			if(this.clm_m_material_location_id=='' || typeof this.clm_m_material_location_id=='undefined'){
				alert('재단 자재의 창고가 빈칸입니다.');
				this.changeLoading_yn='N';
				this.loading_yn='N';
				return;
			}else if(this.clm_m_material_location_position_id=='' || typeof this.clm_m_material_location_position_id=='undefined'){
				alert('재단 자재의 위치가 빈칸입니다.');
				this.changeLoading_yn='N';
				this.loading_yn='N';
				return;
			}
			var end_yn = 'N';
			if(Number(this.m2_cut_usable_count) == Number(this.m2_cut_produce_count)){
				end_yn='Y';
			}
			if(Error_count == 'Y'){
				alert('자재수량은 음수 또는 소수가 될 수 없습니다.');
				this.changeLoading_yn='N';
				this.loading_yn='N';
			}else if(Error_count2 == 'Y'){
				alert('불량수량은 음수 또는 소수가 될 수 없습니다.');
				this.changeLoading_yn='N';
				this.loading_yn='N';
			}else if(Error_count3 == 'Y'){
				alert('자재 수량과 불량수량의 합이 0입니다.');
				this.changeLoading_yn='N';
				this.loading_yn='N';
			}else if(Error_location == 'Y'){
				alert('자재 창고가 빈칸입니다.');
				this.changeLoading_yn='N';
				this.loading_yn='N';
			}else if(Error_position == 'Y'){
				alert('자재 위치가 빈칸입니다.');
				this.changeLoading_yn='N';
				this.loading_yn='N';
			}else{
				axios.get('/back/MOB001002/SelectCompleteCount',{
					params: {
						clm_cut_joborder_id: this.selected_items[0].clm_cut_joborder_id
					}
				}).then(
				response => {
					if (response.status == '200') {
						if(Number(this.m_material_use_count)>Number(response.data)){
							alert('이미 완료된 작업입니다.');
							this.loading_yn='N';			
							this.WorkEndModal=false;
							this.ViewModal=false;
							this.changeLoading_yn='N';
							this.getList();
							this.selected_items=[];
						}else{
							axios.post('/back/MOB001002/WorkFinallyEnd', null, { params: {
								cut_joborder_id: this.m2_cut_joborder_id,
								produce_count:this.m2_cut_produce_count,
								cut_joborder_user:this.m2_cut_joborder_user,
								end_yn:end_yn,
								clm_m_material_location_id:this.clm_m_material_location_id,
								clm_m_material_location_position_id:this.clm_m_material_location_position_id,
								material_id:this.selected_items[0].clm_material_id,
								material_order_id: this.selected_items[0].clm_material_order_id,
								order_income_seq: this.selected_items[0].clm_order_income_seq,
								income_material_seq: this.selected_items[0].clm_income_material_seq,
								material_leftover_seq: this.selected_items[0].clm_material_leftover_seq,
								material_use_count:this.selected_items[0].clm_material_use_count,
								material_full_count: String(Number(this.selected_items[0].clm_usable_count)),
								material_remainder_count:String(Number(this.selected_items[0].clm_usable_count) - Number(this.selected_items[0].clm_material_use_count)),
								holl_size: this.selected_items[0].clm_holl_size,
								user_id: this.user_id,
								partData: partData
							}})
							.then(response => {
								if (response.status == '200') {		
									this.loading_yn='N';				
									this.WorkEndModal=false;
									this.ViewModal=false;
									this.changeLoading_yn='N';
									Swal.fire({
										title:"정상적으로 등록 되었습니다.\n바코드를 프린트하시겠습니까?",
										text:  '',
										icon: 'question',
										showCancelButton: true,
										allowOutsideClick: false,
										confirmButtonColor: '#3085d6',
										cancelButtonColor: '#d33',
										confirmButtonText: '예',
										cancelButtonText: '아니요'
									}).then((result) => {
										if (result.isConfirmed) {
											this.OpenBarcodeSelect();
											this.getList();
											this.selected_items=[];
										}else{
											this.getList();
											this.selected_items=[];
										}
									})
									// if(confirm("정상적으로 등록 되었습니다.\n바코드를 프린트하시겠습니까?")){
									// 	this.OpenBarcodeSelect();
									// }	
								}else{
									this.loading_yn='N';				
									this.changeLoading_yn='N';
									this.getList();
									this.selected_items=[];
								}
							});
						}
					}
				});
			}		
		},
		OpenBarcodeSelect(value){
			this.BarcodeSelectModal=true;
