							}else if(cnt==4 && Number(response.data[x].clm_width)>0 && Number(response.data[x].clm_height)>0 && Number(response.data[x].clm_height)>0 && Number(response.data[x].clm_part_count)>0 && response.data[x].clm_gubun==''){
								this.clm_m3_s4_material_width=Number(response.data[x].clm_width);
								this.clm_m3_s4_material_height=Number(response.data[x].clm_height);
								this.clm_m3_s4_material_count= Number(response.data[x].clm_part_count);
								cnt++;
							}else if(cnt==5 && Number(response.data[x].clm_width)>0 && Number(response.data[x].clm_height)>0 && Number(response.data[x].clm_height)>0 && Number(response.data[x].clm_part_count)>0 && response.data[x].clm_gubun==''){
								this.clm_m3_s5_material_width=Number(response.data[x].clm_width);
								this.clm_m3_s5_material_height=Number(response.data[x].clm_height);
								this.clm_m3_s5_material_count= Number(response.data[x].clm_part_count);
								cnt++;
							}else if(cnt==6 && Number(response.data[x].clm_width)>0 && Number(response.data[x].clm_height)>0 && Number(response.data[x].clm_height)>0 && Number(response.data[x].clm_part_count)>0 && response.data[x].clm_gubun==''){
								this.clm_m3_s6_material_width=Number(response.data[x].clm_width);
								this.clm_m3_s6_material_height=Number(response.data[x].clm_height);
								this.clm_m3_s6_material_count= Number(response.data[x].clm_part_count);
								cnt++;
							}else if(cnt==7 && Number(response.data[x].clm_width)>0 && Number(response.data[x].clm_height)>0 && Number(response.data[x].clm_height)>0 && Number(response.data[x].clm_part_count)>0 && response.data[x].clm_gubun==''){
								this.clm_m3_s7_material_width=Number(response.data[x].clm_width);
								this.clm_m3_s7_material_height=Number(response.data[x].clm_height);
								this.clm_m3_s7_material_count= Number(response.data[x].clm_part_count);
								cnt++;
							}else if(cnt==8 && Number(response.data[x].clm_width)>0 && Number(response.data[x].clm_height)>0 && Number(response.data[x].clm_height)>0 && Number(response.data[x].clm_part_count)>0 && response.data[x].clm_gubun==''){
								this.clm_m3_s8_material_width=Number(response.data[x].clm_width);
								this.clm_m3_s8_material_height=Number(response.data[x].clm_height);
								this.clm_m3_s8_material_count= Number(response.data[x].clm_part_count);
								cnt++;
							}
							if(response.data[x].clm_gubun=='LU'){
								this.LossItems.push({
									clm_width: Number(response.data[x].clm_width),
									clm_height:Number(response.data[x].clm_height),
									clm_thickness:Number(response.data[x].clm_thickness),
									loss_radio:'use',
									clm_loss_count:Number(response.data[x].clm_part_count),
								});
							}else if(response.data[x].clm_gubun=='LL'){
								this.LossItems.push({
									clm_width: Number(response.data[x].clm_width),
									clm_height:Number(response.data[x].clm_height),
									clm_thickness:Number(response.data[x].clm_thickness),
									loss_radio:'loss',
									clm_loss_count:Number(response.data[x].clm_part_count),
								});
							}else if(response.data[x].clm_gubun=='LD'){
								this.LossItems.push({
									clm_width: Number(response.data[x].clm_width),
									clm_height:Number(response.data[x].clm_height),
									clm_thickness:Number(response.data[x].clm_thickness),
									loss_radio:'delete',
									clm_loss_count:Number(response.data[x].clm_part_count),
								});
							}
						}
						this.loss_loading='N';
					}
				})
				.catch(error => {
					console.log(error)
				});	
				this.cutModal=true;
				this.loss_loading='N';
			}else{
				this.m_cut_joborder_datetime=' ';//완료된게 하나라도 있으면 disabled
				this.m_cut_joborder_id = data.item.clm_cut_joborder_id,
				this.m_cut_joborder_seq = data.item.clm_cut_joborder_seq,
				this.m_lotno = data.item.lotno,
				this.m_material_name = data.item.clm_material_name,
				this.m_material_total_use_count= data.item.clm_material_use_count,
				this.m_material_use_count = data.item.clm_current_produce_count,
				this.m_material_location_name = data.item.clm_repository_name,
				this.m_material_location_position_name = data.item.clm_repository_position_name,
				this.m_cut_joborder_user = data.item.clm_cut_joborder_user;
				this.m_part_user_name=data.item.clm_part_user_name;
				this.m_assembly_name=data.item.clm_machine_name;
				axios.get('/back/MOB001002/SubRead',{
				params: {
						clm_cut_joborder_id: this.m_cut_joborder_id,
						clm_cut_joborder_seq: this.m_cut_joborder_seq
					}
				})
				.then(response => {
					this.partItems = [{}],
					this.partItems.splice(0, 1);

					for(var x=0; x<response.data.length; x++){
						this.partItems.push({
							lotno: response.data[x].lotno,
							clm_m_material_id : response.data[x].clm_part_id,
							clm_m_part_width : '0',
							clm_m_part_name : response.data[x].clm_part_name,
							clm_m_part_count : Number(response.data[x].clm_part_count),
							clm_m_part_before_count :Number(response.data[x].clm_part_count),
							clm_m_poor_count : response.data[x].clm_poor_count,
							clm_m_gubun: response.data[x].clm_gubun,
							clm_m_part_count_readonly : true,
							clm_m_part_location_id : '',
							clm_m_part_location_position_id :'',
							clm_m_part_repository_position_id_items : []
						});
					}
				});
				this.loss_loading='N';
				this.ViewModal = true;
			}
		},
		workEnd(){
			var error = 'N';
			var cut_joborder_id = '';
			var material_id = '';
			var material_order_id = '';
			var order_income_seq = '';
			var income_material_seq = '';
			var material_leftover_seq = '';
			var material_use_count = '';
			var material_full_count = '';
			var material_remainder_count = '';
			var holl_size = '';
			
			if(this.selected_items.length == 1) {
				if(this.selected_items[0].clm_cut_joborder_datetime != ''){
					error = 'Y'
				}
				if(cut_joborder_id == ''){
					cut_joborder_id = this.selected_items[0].clm_cut_joborder_id
				}else{
					cut_joborder_id = cut_joborder_id + ',' + this.selected_items[0].clm_cut_joborder_id
				}

				if(material_id == ''){
					material_id = this.selected_items[0].clm_material_id
				}else{
					material_id = material_id + ',' + this.selected_items[0].clm_material_id
				}
				
				if(material_order_id == ''){
					material_order_id = this.selected_items[0].clm_material_order_id
				}else{
					material_order_id = material_order_id + ',' + this.selected_items[0].clm_material_order_id
				}

				if(order_income_seq == ''){
					order_income_seq = this.selected_items[0].clm_order_income_seq
				}else{
					order_income_seq = order_income_seq + ',' + this.selected_items[0].clm_order_income_seq
				}

				if(income_material_seq == ''){
					income_material_seq = this.selected_items[0].clm_income_material_seq
				}else{
					income_material_seq = income_material_seq + ',' + this.selected_items[0].clm_income_material_seq
				}

				if(material_leftover_seq == ''){
					material_leftover_seq = this.selected_items[0].clm_material_leftover_seq
				}else{
					material_leftover_seq = material_leftover_seq + ',' + this.selected_items[0].clm_material_leftover_seq
				}

				if(material_use_count == ''){
					material_use_count = this.selected_items[0].clm_material_use_count
				}else{
					material_use_count = material_use_count + ',' + this.selected_items[0].clm_material_use_count
				}

				if(material_full_count == ''){
					material_full_count = this.selected_items[0].clm_usable_count
				}else{
					material_full_count = material_full_count + ',' + this.selected_items[0].clm_usable_count
				}

				if(material_remainder_count == ''){
					material_remainder_count = String(Number(this.selected_items[0].clm_usable_count) - Number(this.selected_items[0].clm_material_use_count))
				}else{
					material_remainder_count = material_remainder_count + ',' + String(Number(this.selected_items[0].clm_usable_count) - Number(this.selected_items[0].clm_material_use_count))
				}

				if(holl_size == ''){
					holl_size = this.selected_items[0].clm_holl_size
				}else{
					holl_size = holl_size + ',' + this.selected_items[0].clm_holl_size
				}
			}else{
				error = 'YY';
			}
			if(error == 'Y'){
				alert('이미 작업완료된 재단작업이 선택되어 있습니다.');
				return;
			}else if(error == 'YY'){
				alert('선택된 재단작업이 한개가 아닙니다.')
				return;
			}else{
				axios.post('/back/MOB001002/WorkEnd', null, { params: {
					cut_joborder_id: cut_joborder_id,
					material_id: material_id,
					material_order_id: material_order_id,
					order_income_seq: order_income_seq,
					income_material_seq: income_material_seq,
					material_leftover_seq: material_leftover_seq,
					material_use_count: material_use_count,
					material_full_count: material_full_count,
					material_remainder_count: material_remainder_count,
					holl_size: holl_size,
					user_id: this.user_id
				}})
				.then(response => {
					this.getList();
					this.selected_items=[];
				});
			}
		},
	},
}
</script>